<template>
	
	<div class="chedui-1">
		<div class="chedui-top clearfix">
		    <p><span>我的车队 / </span><span>说明说明说明</span></p>
		    <a href="javascript:history.back(-1)">返回上一页 <span class="fa fa-angle-right"></span> </a>
		</div>
	    <div class="chedui-bottom">
	        <chaxunBar></chaxunBar>
	    	<tableBar></tableBar>
	    	<caozuoBar></caozuoBar>
	    </div>
	    <router-view></router-view>
	</div>
</template>

<script>
	import tableBar from '@/components/tableBar';
	import caozuoBar from '@/components/caozuoBar';
	import chaxunBar from '@/components/chaxunBar';
	export default {
		components:{
			tableBar,
			caozuoBar,
			chaxunBar
		},
		data(){
			return{
				input:'',
			}
		}
	}
</script>

<style lang="less">
	.clearfix:after{
		display: block;
		clear: both;
		content: '';
	}
	.chedui-1{
		.chedui-top{
			width: 1340px;
			p:nth-child(1){
				height:90px;
				line-height: 90px;
				color: #8F9DC0;
				float: left;
				span:nth-child(1){
					font-size: 20px;
					font-weight: bold;
					color: #3651A2;
				}
				span:nth-child(2){
					font-size: 14px;
				}
			}
			a{
				float: right;
				line-height: 90px;
				font-size: 14px;
				cursor: pointer;
				&:hover{
					color: #8F9DC0;
				}
				span{
					font-size: 16px;
				}
			}
		}
		
		.chedui-bottom{
			width: 1340px;
			background: #FFFFFF;
			border: 1px solid #CDCDCD;
			padding-bottom: 500px;
			.chaxun-button{
				width: 225px;
				height: 40px;
				margin: 0 auto;
				padding: 10px 0 30px 0;
				button{
					background: #3253AC;
					width: 100%;
					height: 40px;
				}
			}
		}
	}
</style>


