<template>
	<div class="caozuo clearfix">
		<span class="gg">操作功能：</span>
		<ul>
			<router-link tag='li' to='/chedui/chedui-1/chedui-1-1'>
				<el-button type="success" size="mini">新增车辆</el-button>
			</router-link>
			<li>
				<el-button type="success" size="mini" @click="dialogFormVisible = true">发送短信</el-button>
			</li>
			<router-link tag='li' to='/chedui/chedui-1/chedui-1-2'>
				<el-button type="success" size="mini">车辆分享</el-button>
			</router-link>
			<router-link tag='li' to='/chedui/chedui-1/chedui-1-3'>
				<el-button type="success" size="mini">录入运单</el-button>
			</router-link>
		</ul>
		<el-dialog title="发送短信" :visible.sync="dialogFormVisible">
			<el-form :model="form">
				<div class="fasong-box clearfix">
					<span>发送对象：</span>
					<el-input type="textarea" :rows="4" >
					</el-input>
				</div>
				<div class="fasong-box clearfix">
					<span>发送内容：</span>
					<p>字数 <span>( {{textarea.length}} )</span> 条数<span> 2</span></p>
					<el-input type="textarea" :rows="4" placeholder="请输入内容" v-model="textarea">
					</el-input>
				</div>
				<p>说明：</p>
				<p>1、每发送一条短信，将从您的消费账户扣除1000.00元</p>
				<p>2、有钱请玩尽兴</p>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisible = false">取 消</el-button>
				<el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				dialogFormVisible: false,
				form: {
					name: '',
					region: '',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					resource: '',
					desc: ''
				},
				formLabelWidth: '120px',
				textarea: ''
			}
		},
		methods: {

		}
	}
</script>

<style lang="less">
	.clearfix:after {
		display: block;
		clear: both;
		content: '';
	}
	
	.caozuo {
		height: 35px;
		line-height: 35px;
		margin: 40px 30px 30px 30px;
		.gg {
			color: #8F9DC0;
			float: left;
		}
		ul {
			float: left;
			li{
				float: left;
				margin: 0 5px;
			}
		}
	}
	
	.fasong-box {
		span:nth-child(1) {
			float: left;
		}
		p {
			float: right;
			span{
				color: #00BFE0;
			}
		}
		textarea{
			background: #F5F6FB;
		}
	}
</style>


